#toggle-theme {
  display: none;
}

.theme-cange-label {
  background-color: var(--background-color);
  width: 40px;
  height: 20px;
  cursor: pointer;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 50%;
  left: 35%;
  right: 0;
  transform: translateY(-50%);
}

.theme-cange-label:before {
  width: 10px;
  height: 10px;
  border: 2px solid #ff6b08;
  top: 5px;
  left: 5px;
}

.theme-cange-label:after {
  width: 40px;
  height: 20px;
  border: 2px solid #000;
  top: 0;
  right: 0;
}

.theme-cange-label:before {
  content: "";
  box-sizing: border-box;
  border-radius: 50px;
  transition: top .5s, left .5s, right .5s, width .5s, height .5s, border-color .5s;
  position: absolute;
}

.theme-cange-label:after {
  content: "";
  box-sizing: border-box;
  border-radius: 50px;
  transition: top .5s, right .5s, width .5s, height .5s, border-color .5s;
  position: absolute;
}

#toggle-theme:checked + .theme-cange-label:after {
  width: 10px;
  height: 10px;
  border-color: #089cff;
  top: 5px;
  right: 5px;
}

#toggle-theme:checked + .theme-cange-label:before {
  width: 40px;
  height: 20px;
