.main {
  background-color: var(--background-color);
  padding-top: 20px;
  padding-bottom: 20px;
}

.movie-gallery {
  //   outline: 1px solid red;
  @include mobile {
    // max-width: 540px;
    margin: 0 auto;
  }

  @include tablet {
    // max-width: 736px;
    display: flex;
    flex-wrap: wrap;
    margin-left: -32px;
  }

  @include desktop {
    margin-left: -16px;
  }
}

.movie-card {
  margin-bottom: 20px;
  @include tablet {
    flex-basis: calc(100% / 2 - 32px);
    margin-left: 32px;
    margin-bottom: 32px;
  }

  @include desktop {
    flex-basis: calc(100% / 3 - 16px);
    margin-left: 16px;
  }
}

.img-wrap {
  height: 100%;
  overflow: hidden;
  border-radius: 8px;

  @include tablet {
    height: 455px;
  }

  @include desktop {
    height: 574px;
  }
}

.movie-img {
  height: 100%;

  cursor: pointer;

  transform: scale(1);
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}

.movie-name {
  color: var(--color-title);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16;
  text-transform: uppercase;
  margin-top: 10px;

  @include tablet {
    line-height: 1.33;
    margin-top: 8px;
  }

  @include desktop {
    margin-top: 12px;
    font-size: 20px;
    line-height: 1.2;
  }
}

.movie-info {
  display: flex;
  align-items: center;
}

.movie-genre {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  color: var(--accent);

  @include desktop {
    font-size: 20px;
    line-height: 1.2;
  }
}

.movie-raiting {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 8px;
  width: 36px;
  height: 16px;
  border-radius: 5px;
  background-color: var(--accent);
  color: var(--header-color-text);

  @include desktop {
    width: 40px;
    height: 20px;
  }
}
