.library-button__container{
    padding-top: 40px;
    margin-bottom: 5px;
    @include flexCenter()
}

.library-button__watched {
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), filter 250ms cubic-bezier(0.4, 0, 0.2, 1);
    font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
    color: var(--header-color-text);
    border: 2px solid var(--header-border-button-color);
    border-radius: 5px;
    background-color: transparent;
    height: 44px;
    width: 130px;
    margin-right: 20px;
    @include tablet {
        margin-right: 15px;
            width: 152px;
        }

    @include desktop {
        margin-right: 15px;
        width: 148px;
    }
    &:hover {
            border: none;
            background-color: var(--accent);
            box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
                filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        }
}

.library-button__queue{
    font-style: normal;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), filter 250ms cubic-bezier(0.4, 0, 0.2, 1);
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
    color: var(--header-color-text);
        border: 2px solid var(--header-color-text);
        border-radius: 5px;
        background-color: transparent;
    height: 44px;
        width: 130px;
        @include tablet {
            width: 152px;
        }
                @include desktop {
                    width: 148px;
                }
    &:hover{
        border: none;
        background-color: var(--accent);
        box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
}

.library-button__active {
    border: none;
        background-color: var(--accent);
        box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}